import { EvStation, Power, Route } from "@mui/icons-material";
import { Chip, Fab, Grid, Typography } from "@mui/material";
import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import backButton from "./backButton.png";

function Station({ id }) {
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [totalPower, setTotalPower] = useState("");
  const [description, setDescription] = useState("");
  const [guns, setGuns] = useState([]);

  async function stationGet() {
    let Token = localStorage.getItem("tokenAccess");
    let TokenR = localStorage.getItem("tokenRefresh");

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const stationList = await fetch(`http://alvainfo.kz/api/station/${id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      if (stationList.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "http://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          localStorage.setItem("tokenAccess", a.access);
          localStorage.setItem("tokenRefresh", a.refresh);

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      let infoaboutstation = await stationList.json();
      setName(infoaboutstation.name);
      setAddress(infoaboutstation.address);
      setTotalPower(infoaboutstation.total_charging_power);
      setDescription(infoaboutstation.description);
      setGuns(infoaboutstation.guns);
    } catch (e) {
      console.log("Ошибка!");
    }
  }

  useEffect(() => {
    stationGet();
  }, []);

  const navigate = useNavigate();

  return (
    <div>
      <h1> {name} </h1>
      <div className="buttons">
        <img src={backButton} onClick={() => navigate(-1)} />
      </div>
      <p>{description}</p>
      <p>{totalPower} kWh</p>
      <p>{address}</p>

      {guns.map((gun) => {
        return (
          <div className="row">
            <p>
              {gun.type.name}, {gun.charging_power} kWh
            </p>
            <p>{gun.tariff.cost} ₸ в минуту</p>
          </div>
        );
      })}
    </div>
  );
}

export default Station;
