import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import backButton from "./backButton.png";
import mapIcon from "./map.png";
import listIcon from "./list.png";
import profileIcon from "./profile.png";
import mapIconActive from "./mapActive.png";
import listIconActive from "./listActive.png";
import profileIconActive from "./profileActive.png";

function Tabbar(props) {
  let map = mapIcon;
  let list = listIcon;
  let profile = profileIcon;
  let textStyleMap = "tabTextDefault";
  let textStyleList = "tabTextDefault";
  let textStyleProfile = "tabTextDefault";
  if (props.active === "Map") {
    map = mapIconActive;
    textStyleMap = "tabTextActive";
  }

  if (props.active === "List") {
    list = listIconActive;
    textStyleList = "tabTextActive";
  }

  if (props.active === "Profile") {
    profile = profileIconActive;
    textStyleProfile = "tabTextActive";
  }

  return (
    <div class="tabbar">
      <Link to={`/map2`}>
        <div class="tab">
          <img src={map} className="mapimg" />
          <p className={textStyleMap}>
            <b>Карта</b>
          </p>
        </div>
      </Link>
      <Link to={`/list`}>
        <div class="tab">
          <img src={list} className="chargeimg" />
          <p className={textStyleList}>
            <b>Станции</b>
          </p>
        </div>
      </Link>
      <Link to={`/profile`}>
        <div class="tab">
          <img src={profile} className="profileimg" />
          <p className={textStyleProfile}>
            <b>Профиль</b>
          </p>
        </div>
      </Link>
    </div>
  );
}

export default Tabbar;
