import Navigation from "./BottomNavigation";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./index.css";
import sortingButton from "./sortingButton.png";
import filterButton from "./filterButton.png";
import Tabbar from "./tabbar.js";
import ListFilter from "./ListFilter";
import ListSorting from "./ListSorting";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";

export default function List() {
  const { params } = useParams();
  const [stationlist, setStationlist] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  let navigate = useNavigate();

  async function stationGet() {
    let Token;
    let TokenR;
    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const stationList = await fetch("http://alvainfo.kz/api/station/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      if (stationList.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "http://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);

          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      let infoaboutstation = await stationList.json();
      setStationlist(infoaboutstation);
    } catch (e) {
      console.log("Ошибка!");
    }
  }

  useEffect(() => {
    stationGet();
  }, []);

  if (params === "lowprice") {
    stationlist.sort(function (a, b) {
      if (a["guns"][0]["tariff"]["cost"] < b["guns"][0]["tariff"]["cost"]) {
        return 1;
      } else if (
        a["guns"][0]["tariff"]["cost"] === b["guns"][0]["tariff"]["cost"]
      ) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  if (params === "highrating") {
    stationlist.sort(function (a, b) {
      if (a["rating"] < b["rating"]) {
        return 1;
      } else if (a["rating"] === b["rating"]) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  const searchFilter = stationlist.filter((station) => {
    return (
      station.name.toLowerCase().includes(searchValue.toLowerCase()) ||
      station.address.toLowerCase().includes(searchValue.toLowerCase())
    );
  });

  console.log(params);

  return (
    <div>
      <h1>Станции</h1>
      <div className="buttons">
        <form className="search__form">
          <input
            type="text"
            placeholder="Поиск"
            className="search__input"
            onChange={(event) => setSearchValue(event.target.value)}
          />
        </form>
        <Link to={`/list/sorting`}>
          <img src={sortingButton} className="button" />
        </Link>
        <Link to={`/list/filter`}>
          <img src={filterButton} className="button" />
        </Link>
      </div>
      {searchFilter.map((e, i) => {
        console.log(e);
        return (
          <Link to={`/list/station/${e.id}`}>
            <div className="row">
              <p>{e["name"]}</p>
            </div>
          </Link>
        );
      })}
      <Tabbar active="List" />
    </div>
  );
}
