import Navigation from "./BottomNavigation";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./index.css";
import sortingButton from "./sortingButton.png";
import filterButton from "./filterButton.png";
import Tabbar from "./tabbar.js";
import ListFilter from "./ListFilter";
import ListSorting from "./ListSorting";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import submitIcon from "./submit.png";

export default function Registration() {
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [authMistake, setAuthMistake] = useState(false);

  let navigate = useNavigate();

  function onSubmitHandler(e) {
    e.preventDefault();
    console.log("hi!", login, password);

    signUp(login, password, passwordConfirm).then((isSucces) => {
      if (isSucces) {
        signIn(login, password)
          .then((tokensObject) => {
            console.log(tokensObject);

            if (localStorage) {
              localStorage.setItem("tokenAccess", tokensObject.access);
              localStorage.setItem("tokenRefresh", tokensObject.refresh);
            }

            // eslint-disable-next-line no-undef
            if (window.Mobile) {
              // eslint-disable-next-line no-undef
              window.Mobile.consumeToken(JSON.stringify(tokensObject));
            }

            if (window["webkit"]) {
              if (window["webkit"].messageHandlers) {
                if (window["webkit"].messageHandlers.callbackHandler) {
                  window["webkit"].messageHandlers.callbackHandler.postMessage(
                    JSON.stringify(tokensObject)
                  );
                }
              }
            }

            navigate("/list");
          })
          .catch((error) => console.log(error));
      }
    });
  }

  async function signUp(phoneNumber, password, password2) {
    try {
      const response = await fetch("http://alvainfo.kz/api/auth/register/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          phone_number: phoneNumber,
          password: password,
          password2: password2,
          auth_type: "phone_number",
        }),
      });
      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log("401 or badrequest", mistake.phone_number[0]);
        setAuthMistake(true);
        throw new Error("401 or badrequest");
      } else {
        setAuthMistake(false);
      }
      return response.ok;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  async function signIn(phoneNumber, password) {
    try {
      const signInResponse = await fetch("http://alvainfo.kz/api/auth/token/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          phone_number: phoneNumber,
          password: password,
          auth_type: "phone_number",
        }),
      });
      if (signInResponse.status === 401 || !signInResponse.ok) {
        console.log("401 or badrequest");
        setAuthMistake(true);
        throw new Error("401 or badrequest");
      } else {
        setAuthMistake(false);
      }
      const data = await signInResponse.json();
      return data;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  let input_class = authMistake ? "input__mistake" : "input__default";

  return (
    <>
      <div className="headerReg">
        <h1></h1>
        <h1>Регистрация</h1>
        <Link to={`/auth`}>
          <p>Войти</p>
        </Link>
      </div>
      <form className="default__form" onSubmit={onSubmitHandler}>
        <label>
          <input
            className={input_class}
            type="text"
            placeholder="Номер телефона"
            onChange={(event) => setLogin(event.target.value)}
          />
          <input
            className={input_class}
            type="password"
            placeholder="Пароль"
            onChange={(event) => setPassword(event.target.value)}
          />

          <input
            className={input_class}
            type="password"
            placeholder="Подтвердите пароль"
            onChange={(event) => setPasswordConfirm(event.target.value)}
          />
        </label>
        <div className="submitContainer">
          <input
            type="image"
            src={submitIcon}
            name="submit"
            className="submitImage"
          />
        </div>
      </form>
    </>
  );
}
