import Avatar from '@mui/material/Avatar'
import LoadingButton from '@mui/lab/LoadingButton'
import CssBaseline from '@mui/material/CssBaseline'
import TextField from '@mui/material/TextField'
import Box from '@mui/material/Box'
import Link from '@mui/material/Link'
import Grid from '@mui/material/Grid'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import Typography from '@mui/material/Typography'
import Container from '@mui/material/Container'
import { useState } from 'react'

export default function SignUp({ onTokenConsumed, setAccountExists }) {
  const [loading, setLoading] = useState(false)

  const handleSubmit = event => {
    event.preventDefault()
    const data = new FormData(event.currentTarget)
    setLoading(true)
    fetchData({
      'phone_number': data.get('phone'),
      'password': data.get('password'),
      'password2': data.get('passwordConfirmation'),
      'auth_type': 'phone_number'
    }).then(isSuccess => {
      if (!isSuccess) return
      signIn(data.get('phone'), data.get('password')).then(tokensObject => {
        setLoading(false)
        console.log(tokensObject)

        onTokenConsumed(tokensObject.access)

        // eslint-disable-next-line no-undef
        if (window.Mobile) {
          // eslint-disable-next-line no-undef
          window.Mobile.consumeToken(JSON.stringify(tokensObject))
        }

        if (window['webkit']) {
          if (window['webkit'].messageHandlers) {
            if (window['webkit'].messageHandlers.callbackHandler) {
              window['webkit'].messageHandlers.callbackHandler.postMessage(JSON.stringify(tokensObject))
            }
          }
        }
      })
    })
  }

  async function fetchData(body) {
    try {
      // setLoading(true)
      console.log(JSON.stringify(body))
      const response = await fetch(
        'https://alvacharge.kz/api/auth/register/',
        {
          method: 'POST',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          },
          body: JSON.stringify(body)
        }
      )
      return response.ok
    } catch (error) {
      console.error(error)
      return false
    }
  }

  async function signIn(phoneNumber, password) {
    try {
      const signInResponse = await fetch(
        'http://alvacharge.kz/api/auth/token/',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          },
          body: JSON.stringify({
            phone_number: phoneNumber,
            password: password,
            auth_type: 'phone_number'
          })
        }
      )
      const data = await signInResponse.json()
      return data
    } catch (error) {
      console.log(error)
      return null
    }
  }

  return (
    <>
      <Container component='main' maxWidth='xs'>
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component='h1' variant='h5'>
            Sign Up
          </Typography>
          <Box component='form' onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin='normal'
              required
              fullWidth
              id='phone'
              label='Phone number'
              name='phone'
              autoComplete='phone'
              autoFocus
            />
            <TextField
              margin='normal'
              required
              fullWidth
              name='password'
              label='Password'
              type='password'
              id='password'
            />
            <TextField
              margin='normal'
              required
              fullWidth
              name='passwordConfirmation'
              label='Confirm your password'
              type='password'
              id='passwordConfirmation'
            />
            <LoadingButton
              loading={loading}
              type='submit'
              fullWidth
              variant='contained'
              sx={{ mt: 3, mb: 2 }}
            >
              Sign Up
            </LoadingButton>

            <Grid container>
              <Grid item>
                <Link variant="body2" onClick={() => setAccountExists()}>
                  {'Already have a account? Sign In'}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </>
  )
}