import { Box, Button, Container, CssBaseline, Grid, Switch, ToggleButton, ToggleButtonGroup, Typography } from "@mui/material"
import { useState } from 'react'
import { styled } from '@mui/material/styles'
import ChargerType1 from './ic_charger_type_1.svg'
import ChargerType2 from './ic_charger_type_2.svg'

const StyledToggleButtonGroup = styled(ToggleButtonGroup)(({ theme }) => ({
    '& .MuiToggleButtonGroup-grouped': {
        margin: theme.spacing(0.5),
        border: 0,
        '&.Mui-disabled': {
            border: 0,
        },
        '&:not(:first-of-type)': {
            borderRadius: theme.shape.borderRadius,
        },
        '&:first-of-type': {
            borderRadius: theme.shape.borderRadius,
        },
    },
}))
const StyledToggleButton = styled(ToggleButton)({
    "&.Mui-selected, &.Mui-selected:hover": {
        backgroundColor: '#7FDD92'
    }
})

const IOSSwitch = styled((props) => (
    <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
))(({ theme }) => ({
    width: 42,
    height: 26,
    padding: 0,
    '& .MuiSwitch-switchBase': {
        padding: 0,
        margin: 2,
        transitionDuration: '300ms',
        '&.Mui-checked': {
            transform: 'translateX(16px)',
            color: '#fff',
            '& + .MuiSwitch-track': {
                backgroundColor: theme.palette.mode === 'dark' ? '#2ECA45' : '#65C466',
                opacity: 1,
                border: 0,
            },
            '&.Mui-disabled + .MuiSwitch-track': {
                opacity: 0.5,
            },
        },
        '&.Mui-focusVisible .MuiSwitch-thumb': {
            color: '#33cf4d',
            border: '6px solid #fff',
        },
        '&.Mui-disabled .MuiSwitch-thumb': {
            color:
                theme.palette.mode === 'light'
                    ? theme.palette.grey[100]
                    : theme.palette.grey[600],
        },
        '&.Mui-disabled + .MuiSwitch-track': {
            opacity: theme.palette.mode === 'light' ? 0.7 : 0.3,
        },
    },
    '& .MuiSwitch-thumb': {
        boxSizing: 'border-box',
        width: 22,
        height: 22,
    },
    '& .MuiSwitch-track': {
        borderRadius: 26 / 2,
        backgroundColor: theme.palette.mode === 'light' ? '#E9E9EA' : '#39393D',
        opacity: 1,
        transition: theme.transitions.create(['background-color'], {
            duration: 500,
        }),
    },
}))

export default function Filter({ filter, applyFilter }) {
    const [inputs, setInputs] = useState(() => filter.charges)
    const [showOnlyAvailable, setShowOnlyAvailable] = useState(filter.showOnlyAvailable)

    const handleInputs = (_event, newInputs) => {
        if (newInputs.length) {
            setInputs(newInputs)
        }
    }

    return (
        <>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 2,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Typography component="h5" variant="h5">
                        Фильтр
                    </Typography>
                    <StyledToggleButtonGroup
                        margin='normal'
                        color='primary'
                        value={inputs}
                        onChange={handleInputs}
                        aria-label='input'
                        marginTop={2}>
                        <StyledToggleButton value='1' aria-label='1'>
                            <img src={ChargerType1} alt='plug icon' width={40} />
                        </StyledToggleButton>
                        <StyledToggleButton value='2' aria-label='2'>
                            <img src={ChargerType2} alt='plug icon' width={40} />
                        </StyledToggleButton>
                        <StyledToggleButton value='3' aria-label='1'>
                            <img src={ChargerType1} alt='plug icon' width={40} />
                        </StyledToggleButton>
                        <StyledToggleButton value='4' aria-label='2'>
                            <img src={ChargerType2} alt='plug icon' width={40} />
                        </StyledToggleButton>
                        <StyledToggleButton value='5' aria-label='1'>
                            <img src={ChargerType1} alt='plug icon' width={40} />
                        </StyledToggleButton>
                        <StyledToggleButton value='6' aria-label='2'>
                            <img src={ChargerType2} alt='plug icon' width={40} />
                        </StyledToggleButton>
                    </StyledToggleButtonGroup>
                    <Grid container marginTop={2}>
                        <Grid item xs>
                            <Typography>
                                Показывать только доступные
                            </Typography>
                        </Grid>
                        <Grid item>
                            <IOSSwitch checked={showOnlyAvailable} onChange={event => setShowOnlyAvailable(event.target.checked)} />
                        </Grid>
                    </Grid>
                    <Button
                        marginTop={2}
                        type='submit'
                        fullWidth
                        variant='contained'
                        sx={{ mt: 3, mb: 2 }}
                        onClick={() => applyFilter({
                            showOnlyAvailable: showOnlyAvailable,
                            charges: inputs
                        })}>
                        Применить
                    </Button>
                </Box>
            </Container>
        </>
    )
}