import "./App.css";
import "./Map.css";
import { Map, ZoomControl, Overlay } from "pigeon-maps";
import Fab from "@mui/material/Fab";
import NavigationIcon from "@mui/icons-material/Navigation";
import MenuIcon from "@mui/icons-material/Menu";
import ProfileIcon from "./ic_profile.svg";
import WalletIcon from "./ic_wallet.svg";
import HelpIcon from "./ic_help.svg";
import Sheet from "react-modal-sheet";
import { useState, useEffect } from "react";
import {
  Box,
  Card,
  CardContent,
  CardHeader,
  CardMedia,
  createTheme,
  Grid,
  ThemeProvider,
  Typography,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import SignIn from "./SignIn";
import SignUp from "./SignUp";
import Station from "./Station";
import StationForMap from "./StationForMap";
import PlugIcon from "./ic_plug.svg";
import MarkerIcon from "./ic_marker.svg";
import MacMarkerIcon from "./ic_mac_donalds_marker.svg";
import Filter from "./Filter";
import Tabbar from "./tabbar.js";

export default function CustomMap() {
  // TODO move to different component or even class
  const [center, setCenter] = useState([43.238, 76.8829]);
  const [userLocation, setUserLocation] = useState(null); // TODO update by interval
  const [stations, setStations] = useState(null);
  const [isMenuOpened, setMenuIsOpened] = useState(false);
  const [isFilterOpened, setFilterIsOpened] = useState(false);
  const [currentStation, setCurrentStation] = useState(null);
  const [token, setToken] = useState(null);
  const [hasAccount, setHasAccount] = useState(true);
  const [filter, setFilter] = useState({
    showOnlyAvailable: true,
    charges: ["1", "2"],
  });
  const [stationlist, setStationlist] = useState([]);

  async function stationGet() {
    let Token;

    let TokenR;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const stationList = await fetch("http://alvainfo.kz/api/station/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      if (stationList.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "http://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      let infoaboutstation = await stationList.json();
      setStationlist(infoaboutstation);
    } catch (e) {
      console.log("Ошибка!");
    }
  }

  useEffect(() => {
    stationGet();
  }, []);

  if (stations == null) {
    setStations([
      {
        id: 1,
        location: [43.238, 76.8829],
        distance: "5 km",
        volume: "60kWh",
        volumeText: "60",
        isMac: false,
      },
      {
        id: 2,
        location: [43.22, 76.8737],
        distance: "6 km",
        volume: "120kWh",
        volumeText: "120",
        isMac: false,
      },
      {
        id: 3,
        location: [43.23, 76.88],
        distance: "6 km",
        volume: "60kWh",
        volumeText: "60",
        isMac: false,
      },
      {
        id: 2,
        location: [43.24, 76.896],
        distance: "6 km",
        volume: "120kWh",
        volumeText: "100",
        isMac: true,
      },
      // {
      //   id: 3,
      //   location: []
      // }
    ]);
  }

  const theme = createTheme();
  const LocationFab = styled(Fab)(({ theme }) => ({
    position: "fixed",
    bottom: theme.spacing(1),
    right: theme.spacing(2),
  })); // TODO move to separated component ???
  const FilterFab = styled(Fab)(({ theme }) => ({
    position: "fixed",
    bottom: theme.spacing(1.8),
    left: "45%",
  }));
  const FilterBackground = styled(Fab)(({ theme }) => ({
    position: "fixed",
    bottom: theme.spacing(1),
    width: 70,
    height: 70,
    left: "43.4%",
  }));

  function getCurrentUserPosition() {
    navigator.geolocation.getCurrentPosition(
      ({ coords }) => {
        const { latitude, longitude } = coords;
        const location = [latitude, longitude];
        setCenter(location);
        setUserLocation(location);
      },
      () => console.log("Unable to retrieve your location")
    );
  }

  function onStationMarkClicked(id, coordinates) {
    setCurrentStation(id);
    setCenter([coordinates[0], coordinates[1]]);
  }

  function CurrentLocationMap() {
    const userLocationPointSize = 14;
    const userPointOffset = userLocationPointSize / 2;

    return (
      <Map center={center} defaultZoom={11}>
        {userLocation && (
          <Overlay
            anchor={userLocation}
            offset={[userPointOffset + 6, userPointOffset + 6]}
          >
            <div
              style={{
                display: "fixed",
                width: `${userLocationPointSize + 12}px`,
                height: `${userLocationPointSize + 12}px`,
                backgroundColor: "#0080FF40",
                borderRadius: "50%",
              }}
            />
          </Overlay>
        )}
        {userLocation && (
          <Overlay
            anchor={userLocation}
            offset={[userPointOffset + 1, userPointOffset + 1]}
          >
            <div
              style={{
                display: "fixed",
                width: `${userLocationPointSize + 2}px`,
                height: `${userLocationPointSize + 2}px`,
                backgroundColor: "white",
                borderRadius: "50%",
              }}
            />
          </Overlay>
        )}
        {userLocation && (
          <Overlay
            anchor={userLocation}
            offset={[userPointOffset, userPointOffset]}
          >
            <div
              style={{
                display: "fixed",
                width: `${userLocationPointSize}px`,
                height: `${userLocationPointSize}px`,
                backgroundColor: "#006699CC",
                borderRadius: "50%",
              }}
            />
          </Overlay>
        )}
        {stationlist.map((station) =>
          station.isMac ? (
            <Overlay
              key={station.id}
              anchor={[station.latitude, station.longitude]}
              offset={[24, 66]}
            >
              <img
                src={MacMarkerIcon}
                alt="marker"
                width={48}
                onClick={() => onStationMarkClicked(station.id)}
              />
            </Overlay>
          ) : (
            <Overlay
              key={station.id}
              anchor={[station.latitude, station.longitude]}
              offset={[22, 52]}
            >
              <img
                src={MarkerIcon}
                alt="marker"
                width={50}
                onClick={() => onStationMarkClicked(station.id)}
              />
            </Overlay>
          )
        )}
        {stationlist.map((station) =>
          station.isMac ? (
            <Overlay
              key={station.id}
              anchor={[station.latitude, station.longitude]}
              offset={[
                String(station.total_charging_power).length < 3 ? 7 : 11,
                38,
              ]}
              onClick={() => onStationMarkClicked(station.id)}
            >
              <Typography
                variant="caption"
                onClick={() => onStationMarkClicked(station.id)}
                color="#038A00"
              >
                {station.total_charging_power}
              </Typography>
            </Overlay>
          ) : (
            <Overlay
              key={station.id}
              anchor={[station.latitude, station.longitude]}
              offset={[
                String(station.total_charging_power).length < 3 ? 6 : 11,
                41,
              ]}
              onClick={() => onStationMarkClicked(station.id)}
            >
              <Typography
                onClick={() =>
                  onStationMarkClicked(station.id, [
                    station.latitude,
                    station.longitude,
                  ])
                }
              >
                {station.total_charging_power}
              </Typography>
            </Overlay>
          )
        )}
        <ZoomControl />
      </Map>
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <CurrentLocationMap />
        <Sheet
          isOpen={currentStation}
          onClose={() => setCurrentStation(null)}
          detent="content"
        >
          <Sheet.Container>
            <Sheet.Content>
              <StationForMap id={currentStation} />
            </Sheet.Content>
          </Sheet.Container>
        </Sheet>
        <Sheet
          isOpen={isFilterOpened}
          onClose={() => setFilterIsOpened(false)}
          detent="content"
        >
          <Sheet.Container>
            <Sheet.Content>
              <Filter
                filter={filter}
                applyFilter={(filter) => {
                  setFilter(filter);
                  setFilterIsOpened(false);
                }}
              />
            </Sheet.Content>
          </Sheet.Container>
        </Sheet>
      </div>

      <Tabbar active="Map" />
    </ThemeProvider>
  );
}
