import './App.css'
import './Map.css'
import { Map, ZoomControl, Overlay } from 'pigeon-maps'
import Fab from '@mui/material/Fab'
import NavigationIcon from '@mui/icons-material/Navigation'
import MenuIcon from '@mui/icons-material/Menu'
import ProfileIcon from './ic_profile.svg'
import WalletIcon from './ic_wallet.svg'
import HelpIcon from './ic_help.svg'
import Sheet from 'react-modal-sheet'
import { useState } from 'react'
import { Box, Card, CardContent, CardHeader, CardMedia, createTheme, Grid, ThemeProvider, Typography } from '@mui/material'
import { styled } from '@mui/material/styles'
import SignIn from './SignIn'
import SignUp from './SignUp'
import Station from './Station'
import PlugIcon from './ic_plug.svg'
import MarkerIcon from './ic_marker.svg'
import MacMarkerIcon from './ic_mac_donalds_marker.svg'
import Filter from './Filter'

export default function CustomMap() { // TODO move to different component or even class
  const [center, setCenter] = useState([43.2380, 76.8829])
  const [userLocation, setUserLocation] = useState(null) // TODO update by interval
  const [stations, setStations] = useState(null)
  const [isMenuOpened, setMenuIsOpened] = useState(false)
  const [isFilterOpened, setFilterIsOpened] = useState(false)
  const [currentStation, setCurrentStation] = useState(null)
  const [token, setToken] = useState(null)
  const [hasAccount, setHasAccount] = useState(true)
  const [filter, setFilter] = useState({
    showOnlyAvailable: true,
    charges: ['1', '2']
  })

  if (stations == null) {
    setStations([
      {
        id: 1,
        location: [43.2380, 76.8829],
        distance: '5 km',
        volume: '60kWh',
        volumeText: '60',
        isMac: false
      },
      {
        id: 2,
        location: [43.2200, 76.8737],
        distance: '6 km',
        volume: '120kWh',
        volumeText: '120',
        isMac: false
      },
      {
        id: 3,
        location: [43.2300, 76.8800],
        distance: '6 km',
        volume: '60kWh',
        volumeText: '60',
        isMac: false
      },
      {
        id: 2,
        location: [43.2400, 76.8960],
        distance: '6 km',
        volume: '120kWh',
        volumeText: '100',
        isMac: true
      },
      // {
      //   id: 3,
      //   location: []
      // }
    ])
  }

  const theme = createTheme()
  const LocationFab = styled(Fab)(({ theme }) => ({
    position: 'fixed',
    bottom: theme.spacing(1),
    right: theme.spacing(2)
  })) // TODO move to separated component ???
  const MenuFab = styled(Fab)(({ theme }) => ({
    position: 'fixed',
    bottom: theme.spacing(1),
    left: theme.spacing(2)
  }))
  const FilterFab = styled(Fab)(({ theme }) => ({
    position: 'fixed',
    bottom: theme.spacing(1.8),
    left: '45%'
  }))
  const FilterBackground = styled(Fab)(({ theme }) => ({
    position: 'fixed',
    bottom: theme.spacing(1),
    width: 70,
    height: 70,
    left: '43.4%'
  }))

  function getCurrentUserPosition() {
    navigator.geolocation.getCurrentPosition(
      ({ coords }) => {
        const { latitude, longitude } = coords
        const location = [latitude, longitude]
        setCenter(location)
        setUserLocation(location)
      },
      () => console.log('Unable to retrieve your location')
    )
  }

  function onStationMarkClicked(station) {
    setCurrentStation(station)
    setCenter(station.location)
  }

  function CurrentLocationMap() {
    const userLocationPointSize = 14
    const userPointOffset = userLocationPointSize / 2

    return (
      <Map center={center} defaultZoom={15}>
        {userLocation &&
          <Overlay anchor={userLocation} offset={[userPointOffset + 6, userPointOffset + 6]}>
            <div style={{ display: 'fixed', width: `${userLocationPointSize + 12}px`, height: `${userLocationPointSize + 12}px`, backgroundColor: '#0080FF40', borderRadius: '50%' }} />
          </Overlay>
        }
        {userLocation &&
          <Overlay anchor={userLocation} offset={[userPointOffset + 1, userPointOffset + 1]}>
            <div style={{ display: 'fixed', width: `${userLocationPointSize + 2}px`, height: `${userLocationPointSize + 2}px`, backgroundColor: 'white', borderRadius: '50%' }} />
          </Overlay>
        }
        {userLocation &&
          <Overlay anchor={userLocation} offset={[userPointOffset, userPointOffset]}>
            <div style={{ display: 'fixed', width: `${userLocationPointSize}px`, height: `${userLocationPointSize}px`, backgroundColor: '#006699CC', borderRadius: '50%' }} />
          </Overlay>
        }
        {stations.map(station =>
          station.isMac
            ? <Overlay key={station.id} anchor={station.location} offset={[24, 66]}>
              <img src={MacMarkerIcon} alt='marker' width={48} onClick={() => onStationMarkClicked(station)} />
            </Overlay>
            : <Overlay key={station.id} anchor={station.location} offset={[22, 52]}>
              <img src={MarkerIcon} alt='marker' width={44} onClick={() => onStationMarkClicked(station)} />
            </Overlay>
        )}
        {stations.map(station =>
          station.isMac
            ? <Overlay key={station.id} anchor={station.location} offset={[station.volumeText.length < 3 ? 7 : 11, 38]} onClick={() => onStationMarkClicked(station)}>
              <Typography variant='caption' onClick={() => onStationMarkClicked(station)} color='#038A00'>{station.volumeText}</Typography>
            </Overlay>
            : <Overlay key={station.id} anchor={station.location} offset={[station.volumeText.length < 3 ? 9 : 15, 43]} onClick={() => onStationMarkClicked(station)}>
              <Typography onClick={() => onStationMarkClicked(station)}>{station.volumeText}</Typography>
            </Overlay>
        )}
        {stations.map(station =>
          station.isMac
            ? <Overlay key={station.id} anchor={station.location} offset={[80, 120]} onClick={() => onStationMarkClicked(station)}>
              <h6>Мен Даниярмын</h6>
            </Overlay>
            : <></>
        )}
        <ZoomControl />
      </Map>
    )
  }

  return (
    <ThemeProvider theme={theme}>
      <div className='App'>
        <CurrentLocationMap />
        <MenuFab size='small' onClick={() => setMenuIsOpened(true)}>
          <MenuIcon />
        </MenuFab>
        <FilterBackground style={{ backgroundColor: '#FFFFFF' }} />
        <FilterFab size='large' sx={{ boxShadow: 0 }} onClick={() => setFilterIsOpened(true)}>
          <img src={PlugIcon} alt='plug icon' width={65} height={65} />
        </FilterFab>
        <LocationFab size='small' onClick={() => getCurrentUserPosition()}>
          <NavigationIcon />
        </LocationFab>
        <Sheet isOpen={token == null} disableDrag={true}>
          <Sheet.Container>
            <Sheet.Content>
              {
                hasAccount
                  ? <SignIn onTokenConsumed={accessToken => setToken(accessToken)} setNoAccount={() => setHasAccount(false)} />
                  : <SignUp onTokenConsumed={accessToken => setToken(accessToken)} setAccountExists={() => setHasAccount(true)} />
              }
            </Sheet.Content>
          </Sheet.Container>
        </Sheet>
        <Sheet isOpen={isMenuOpened} onClose={() => setMenuIsOpened(false)} detent='content'>
          <Sheet.Container style={{ background: '#EEEEEE' }}>
            <Sheet.Content>
              <Menu />
            </Sheet.Content>
          </Sheet.Container>
        </Sheet>
        <Sheet isOpen={currentStation} onClose={() => setCurrentStation(null)} detent='content'>
          <Sheet.Container>
            <Sheet.Content>
              <Station station={currentStation} />
            </Sheet.Content>
          </Sheet.Container>
        </Sheet>
        <Sheet isOpen={isFilterOpened} onClose={() => setFilterIsOpened(false)} detent='content'>
          <Sheet.Container>
            <Sheet.Content>
              <Filter filter={filter} applyFilter={filter => { setFilter(filter); setFilterIsOpened(false) }} />
            </Sheet.Content>
          </Sheet.Container>
        </Sheet>
      </div>
    </ThemeProvider>
  )
}

function Menu() {
  return (
    <>
      <Grid container spacing={2} m={2}>
        <Grid item xs={10}>
          <Card sx={{ display: 'flex' }} elevation={0}>
            <Box sx={{ display: 'flex', flexDirection: 'column' }}>
              <CardContent sx={{ flex: '1 0 auto' }}>
                <Typography component="b" variant="title">
                  Профиль
                </Typography>
                <Typography variant="subtitle1" color="text.secondary" component="div">
                  +7 (777) 111-11-11
                </Typography>
              </CardContent>
            </Box>
            <CardMedia component='img' image={ProfileIcon} sx={{ width: 64, marginLeft: 18 }} />
          </Card>
        </Grid>

        <Grid item xs={5}>
          <Card elevation={0}>
            <CardHeader avatar={<img src={WalletIcon} alt='wallet' />} />
            <CardContent>
              <Typography component="b" variant="title">
                Кошелек
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={5}>
          <Card elevation={0}>
            <CardHeader avatar={<img src={HelpIcon} alt='wallet' />} />
            <CardContent>
              <Typography component="b" variant="title">
                Помошь
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </>
  )
}