import Avatar from "@mui/material/Avatar";
import LoadingButton from "@mui/lab/LoadingButton";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { useState } from "react";

export default function SignIn({ onTokenConsumed, setNoAccount }) {
  const [loading, setLoading] = useState(false);
  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);
    const data = new FormData(event.currentTarget);
    signIn(data.get("phone"), data.get("password")).then((tokensObject) => {
      setLoading(false);

      onTokenConsumed(tokensObject.access);

      console.log(tokensObject);

      localStorage.setItem("tokenAccess", tokensObject.access);
      localStorage.setItem("tokenRefresh", tokensObject.refresh);

      // eslint-disable-next-line no-undef
      if (window.Mobile) {
        // eslint-disable-next-line no-undef
        window.Mobile.consumeToken(JSON.stringify(tokensObject));
      }

      if (window["webkit"]) {
        if (window["webkit"].messageHandlers) {
          if (window["webkit"].messageHandlers.callbackHandler) {
            window["webkit"].messageHandlers.callbackHandler.postMessage(
              JSON.stringify(tokensObject)
            );
          }
        }
      }
    });
  };

  async function signIn(phoneNumber, password) {
    try {
      const signInResponse = await fetch("http://alvainfo.kz/api/auth/token/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          phone_number: phoneNumber,
          password: password,
          auth_type: "phone_number",
        }),
      });
      const data = await signInResponse.json();
      return data;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  return (
    <>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required
              fullWidth
              id="phone"
              label="Phone number"
              name="phone"
              autoComplete="phone"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            />
            <LoadingButton
              loading={loading}
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign In
            </LoadingButton>
            <Grid container>
              <Grid item xs>
                <Link variant="body2">Forgot password?</Link>
              </Grid>
              <Grid item>
                <Link variant="body2" onClick={() => setNoAccount()}>
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </>
  );
}
